
import FlexCalDayViewModel from './FlexCalDayViewModel';

export default class FlexCalMonthViewModel {

    flexCalDayList: FlexCalDayViewModel[] = [];

    get daysPartitionedByWeek(): FlexCalDayViewModel[][] {

        //console.log(`daysPartitionedByWeek...0...${this.flexCalDayList.length / 7}`);
        var results : FlexCalDayViewModel[][] = [];
        for (let i=0; i < (this.flexCalDayList.length / 7); i++) {
            //console.log(`daysPartitionedByWeek...10...${i}, ${this.flexCalDayList.slice(i*7, (i+1)*7)}...`);
            results.push(this.flexCalDayList.slice(i*7, (i+1)*7));
        }
        return results;
    }
}

