
import { CalDateLevelTypeConstants, FlexCalLangChoice } from '../model/FlexCalConstants';
import FlexCalDayViewModel from '../model/FlexCalDayViewModel';

export const ACTION_GO_PREV_DAY = "GO_PREV_DAY"
export const ACTION_GO_NEXT_DAY = "GO_NEXT_DAY"
export const ACTION_GO_PREV_WEEK = "GO_PREV_WEEK"
export const ACTION_GO_NEXT_WEEK = "GO_NEXT_WEEK"
export const ACTION_GO_PREV_MONTH = "GO_PREV_MONTH"
export const ACTION_GO_NEXT_MONTH = "GO_NEXT_MONTH"
export const ACTION_GO_PREV_YEAR = "GO_PREV_YEAR"
export const ACTION_GO_NEXT_YEAR = "GO_NEXT_YEAR"
export const ACTION_GO_DAY_OF_YEAR = "GO_DAY_OF_YEAR"
export const ACTION_GO_DAY_OF_DAILY_VIEW = "ACTION_GO_DAY_OF_DAILY_VIEW"
export const ACTION_SET_CURRENT_DATE = "ACTION_SET_CURRENT_DATE"
export const ACTION_DAY_CONTENT_CHANGED = "ACTION_DAY_CONTENT_CHANGED"
export const ACTION_SET_CAL_VIEW_TYPE = "SET_CAL_VIEW_TYPE"
export const ACTION_SET_LANGUAGE = "SET_LANGUAGE"

export type FlexCalAction = | {
    type: typeof ACTION_GO_PREV_MONTH
}
| {
    type: typeof ACTION_GO_NEXT_MONTH
}
| {
    type: typeof ACTION_GO_PREV_YEAR
}
| {
    type: typeof ACTION_GO_NEXT_YEAR
}
| {
    type: typeof ACTION_GO_DAY_OF_YEAR,
    payload: FlexCalDayViewModel,
}
| {
    type: typeof ACTION_GO_DAY_OF_DAILY_VIEW,
    payload: Date,
}
| {
    type: typeof ACTION_GO_PREV_WEEK
}
| {
    type: typeof ACTION_GO_NEXT_WEEK
}
| {
    type: typeof ACTION_GO_PREV_DAY
}
| {
    type: typeof ACTION_GO_NEXT_DAY
}
| {
    type: typeof ACTION_SET_CAL_VIEW_TYPE,
    payload: CalDateLevelTypeConstants
}
| {
    type: typeof ACTION_SET_LANGUAGE,
    payload: FlexCalLangChoice
}
| {
    type: typeof ACTION_SET_CURRENT_DATE,
    payload: Date,
}
;
