import '../css/FlexCalendar.css';
import React, { useContext, useLayoutEffect, useRef } from 'react';
import {useState, useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from '../model/CalDateHelper';
import { stringSelector, curDateSelector, stateSelector, curMonthViewModelSelector, todayDateSelector } from '../redux/FlexCalSelectors';
import { actionGoNextMonth, actionGoPrevMonth, actionSetCurrentDate } from '../redux/FlexCalActionCreators';
import FlexCalMonthViewModel from '../model/FlexCalMonthViewModel';
import CalendarMonthContentCell from '../external/CalendarMonthContentCell';
import { PrevNextView } from '../view/PrevNextView';
import { TestCalMonthContentCell } from '../external/TestCalMonthContentCell';
import { useContainerDimensions } from '../hooks/useContainerDimensions';
import {getResponsiveTextSize, getResponsiveTitleSize} from '../responsive/responsive';
import FlexCalMonthViewHeaderBig from './FlexCalMonthViewHeaderBig';
import { useSortable } from '../../Sortable/useSortable';

export default function FlexCalMonthViewContainerBig(props: any) {

  console.log(`FlexCalMonthViewContainerBig...0...${props.date}...`);

  const componentRef = useRef<any>();
  const { width, height } = useContainerDimensions(componentRef);
    
  
  //
  const dispatch = useDispatch();
  const strings = useSelector(stringSelector);
  const curDate = useSelector(curDateSelector);
  const todayDate = useSelector(todayDateSelector);
   
  const storeState = useSelector(stateSelector);
  const weekLabels = [
      strings.sun,
      strings.mon,
      strings.tue,
      strings.wed,
      strings.thu,
      strings.fri,
      strings.sat,
  ];
  const monthLabels = [
    strings.jan,
    strings.feb,
    strings.mar,
    strings.apr,
    strings.may,
    strings.jun,
    strings.jul,
    strings.aug,
    strings.sep,
    strings.oct,
    strings.nov,
    strings.dec,
];


  //
   const calMonthViewModel: FlexCalMonthViewModel =  useSelector(curMonthViewModelSelector);
   const weeks = calMonthViewModel.daysPartitionedByWeek;


  function doPrev() {
    console.log('do prev...');
    dispatch(actionGoPrevMonth());
  
  }
  function doNext() {
    console.log('do next...');
    dispatch(actionGoNextMonth());
  
  }

  const onChildClick = (e: any, date: Date) => {
    console.log(`FlexCalApp::getDayContentCells::onClick==... ...`);
   
    e.preventDefault();
    e.stopPropagation();

    dispatch(actionSetCurrentDate(date));
}


  if (props.children) {
    console.log(`props.children COUNT == ${props.children.length}`);
  }
  else {
    console.log(`props.children COUNT == 0 !!!`);
  }
   

  return (
 
    <div ref={ componentRef } className=" FlexCalMonthContainer"> 
 
    {
    /*
    <div className="FlexCalMonthHeader  sticky-to-top-under ">

 
        <PrevNextView doPrev={ ()=>{ doPrev(); } } doNext={ ()=>{ doNext(); } }>
        
        <span style={{ fontSize: getResponsiveTitleSize(width) }}>
          { monthLabels[ curDate.getMonth() ] }  
          &nbsp;
          { curDate.getFullYear() }
        </span>

        </PrevNextView>

        </div>
    */
    }
 
    <FlexCalMonthViewHeaderBig width={ width }></FlexCalMonthViewHeaderBig>
       

      <br/>

      <div className="FlexCalMonthBody"> 
 
      {
        weekLabels.map((label)=><div key={ 'WEEK_' + label } className="FlexCalShowWhenBigControl  FlexCalDaySlotHeader border"><span>{label}</span></div>)
      }


      { 
        weeks.map((week) => {
           
          return week.map((day, index) => {

            if (day.isCurrentMonth == true) {
              return (

                <div  
                    key={ 'M_' + index } 
                    onClick={ (e: any)=> onChildClick(e, day.date)} 

                    className= {` FlexCalDaySlotCell border enabled-style ${dateHelper.isSameDate(day.date, curDate) ? "selected-cell" : ""} `}>

                    <div  className='  FlexCalSlotContainer'>

                      {

                      props.children && props.children.filter((comp: any)=>{
                        const comp_date = new Date(comp.props.date);

                        return (comp_date.getFullYear() === day.date.getFullYear() 
                            && (comp_date.getMonth() === day.date.getMonth())
                            && (comp_date.getDate() === day.date.getDate())
                        );
                      })

                      }
                     
                    </div> 
                    
                </div>
                  
              )
            } 
            else {
              return (
  
                <div key={ 'EXCL_' + day.keyLabel } className="FlexCalShowWhenBigControl  border disabed-style" >
                  <div className="  FlexCalDaySlotCell   " >
                    <div> {day.dayLabel} </div> 
                </div>
                </div>
                  
              )
            }
            
             

          })
          
        })
      }
      </div>


    </div>
  );
}

 