import { useRef, useState } from 'react';
import styled from 'styled-components';
import { useContainerDimensions } from '../hooks/useContainerDimensions';

const ModalBackground = styled.div`
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
	 
`;

const ModalBody = styled.div`
	background-color: white;
	margin: 10% auto;
	width: 50%;
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.8);

	border-radius: 10px;
`;


export const PopupModal = ({ handleClosedEvent, children }: {
    [key: string]: any,
    handleClosedEvent: (e: any)=>void
}) => {

	 
	return (
		<>
		
		 
		
		{(
			<ModalBackground className='popup' onClick={(e: any) => handleClosedEvent(e)}>
				<ModalBody onClick={e => {e.preventDefault(); e.stopPropagation();}}>
					<button   onClick={(e: any) => handleClosedEvent(e)}><i className="fa fa-close"></i></button>
					{children}
				</ModalBody>
			</ModalBackground>
		)}
		
		</>
	);
}


