import { initialState } from "./FlexCalStore"
import IFlexCalState from '../model/IFlexCalState';
import * as Actions from './FlexCalActionTypes';
import { dateHelper } from "../model/CalDateHelper";
import { localStorageHelper } from "../model/LocalStorageHelper";
import { CalDateLevelTypeConstants, FlexCalLangChoice, KEY_LANGUAGE_CHOICE } from '../model/FlexCalConstants';
import StringZh from "../strings/string-zh";
import StringEn from "../strings/string-en";
import CalendarMonthContentCell from '../external/CalendarMonthContentCell';
import { getMonthContentCells } from "../FlexCalApp";



const flexCalReducer = (
    state: IFlexCalState = initialState,
    action: Actions.FlexCalAction
  ): IFlexCalState => {

     console.log(`flexCalReducer...0...${JSON.stringify(action)}...`);

    switch (action.type) {

        case Actions.ACTION_GO_PREV_MONTH:

            const prev_date = dateHelper.getFirstDateOfPreviousMonth(state.curDate);
            
            return {
                ...state,
                todayDate: new Date(),
                curDate: prev_date,
                monthViewModel: dateHelper.getMonthCalendarViewModel(prev_date),
                
            }

        case Actions.ACTION_GO_NEXT_MONTH:

            const next_date = dateHelper.getFirstDateOfNextMonth(state.curDate);

            return {
                ...state,
                curDate: next_date,
                todayDate: new Date(),
                monthViewModel: dateHelper.getMonthCalendarViewModel(next_date),
                
            };

        case Actions.ACTION_GO_PREV_WEEK:

            const before_prev_cur_date = state.curDate;
            const prev_week_date = dateHelper.getDateOfPreviousWeek(state.curDate);
                
            if (dateHelper.isSameMonth(before_prev_cur_date, prev_week_date)) {
                return {
                    ...state,
                    curDate: prev_week_date,
                    todayDate: new Date(),
                };
            } 
            else {
                return {
                    ...state,
                    curDate: prev_week_date,
                    todayDate: new Date(),
                    monthViewModel: dateHelper.getMonthCalendarViewModel(prev_week_date),
                     
                };
            }
            

        case Actions.ACTION_GO_NEXT_DAY:

                const before_nextday_curdate = state.curDate;
                const next_day_date = dateHelper.getDateOfNextDay(state.curDate);
                        
                if (dateHelper.isSameMonth(next_day_date, before_nextday_curdate)) {
                    return {
                        ...state,
                        todayDate: new Date(),
                        curDate: next_day_date,
                    };
                } 
                else {
                    return {
                        ...state,
                        todayDate: new Date(),
                        curDate: next_day_date,
                        monthViewModel: dateHelper.getMonthCalendarViewModel(next_day_date),
                        
                    };
                }


        case Actions.ACTION_GO_PREV_DAY:

            const before_prevday_curdate = state.curDate;
            const prev_day_date = dateHelper.getDateOfPreviousDay(state.curDate);
                    
            if (dateHelper.isSameMonth(prev_day_date, before_prevday_curdate)) {
                return {
                    ...state,
                    todayDate: new Date(),
                    curDate: prev_day_date,
                };
            } 
            else {
                return {
                    ...state,
                    todayDate: new Date(),
                    curDate: prev_day_date,
                    monthViewModel: dateHelper.getMonthCalendarViewModel(prev_day_date),
                    
                };
            }

        case Actions.ACTION_GO_NEXT_WEEK:

            const before_next_cur_date = state.curDate;
            const next_week_date = dateHelper.getDateOfNextWeek(state.curDate);
                
            if (dateHelper.isSameMonth(before_next_cur_date, next_week_date)) {
                return {
                    ...state,
                    todayDate: new Date(),
                    curDate: next_week_date,
                };
            }
            else {
                return {
                    ...state,
                    todayDate: new Date(),
                    curDate: next_week_date,
                    monthViewModel: dateHelper.getMonthCalendarViewModel(next_week_date),
                    
                };
            }
 
        case Actions.ACTION_GO_PREV_YEAR:

            const prev_year_date = dateHelper.getPreviousYear(state.curDate);
            
            return {
                ...state,
                todayDate: new Date(),
                curDate: prev_year_date,
                monthViewModel: dateHelper.getMonthCalendarViewModel(prev_year_date),
               
            }

        case Actions.ACTION_GO_NEXT_YEAR:

            const next_year_date = dateHelper.getNextYear(state.curDate);
            
            console.log(`ACTION_GO_DAY_OF_YEAR...next_year_date===${next_year_date}...`);


            return {
                ...state,
                todayDate: new Date(),
                curDate: next_year_date,
                monthViewModel: dateHelper.getMonthCalendarViewModel(next_year_date),
               
            }

        case Actions.ACTION_GO_DAY_OF_YEAR:

            const new_date = action.payload.date
             
            console.log(`ACTION_GO_DAY_OF_YEAR...new_date===${new_date}...`);

 
            return {
                ...state,
                calDateLevelType: CalDateLevelTypeConstants.Month,
                todayDate: new Date(),
                curDate: new_date,
                monthViewModel: (dateHelper.isSameMonth(state.curDate, new_date)) ? 
                    state.monthViewModel : dateHelper.getMonthCalendarViewModel(new_date),
                
            };


        case Actions.ACTION_GO_DAY_OF_DAILY_VIEW:

            const daily_date = action.payload
             
            console.log(`ACTION_GO_DAY_OF_DAILY_VIEW...new_date===${daily_date}...`);

 
            return {
                ...state,
                calDateLevelType: CalDateLevelTypeConstants.Day,
                todayDate: new Date(),
                curDate: daily_date,
                monthViewModel: (dateHelper.isSameMonth(state.curDate, daily_date)) ? 
                    state.monthViewModel : dateHelper.getMonthCalendarViewModel(daily_date),
            };


            
        case Actions.ACTION_SET_CURRENT_DATE:

            const cur_date = action.payload;
            
            console.log(`ACTION_SET_CURRENT_DATE...cur_date===${cur_date}...isEameMonth==${(dateHelper.isSameMonth(state.curDate, cur_date))}...`);
 
         
            return {
                ...state,
                todayDate: new Date(),
                curDate: cur_date,
              
            };
          
            
            return state;
            

            
        case Actions.ACTION_SET_LANGUAGE:

            localStorageHelper.setValue(KEY_LANGUAGE_CHOICE, action.payload);
            let new_strings: {[key: string]: string}
            if (localStorageHelper.getValue(KEY_LANGUAGE_CHOICE) == FlexCalLangChoice.Zh) {
                new_strings = (new StringZh()).constants;
            }
            else {
                new_strings = (new StringEn()).constants;
            };

            return {
                ...state,
                todayDate: new Date(),
                curLang: action.payload,
                strings: new_strings,
            };

        case Actions.ACTION_SET_CAL_VIEW_TYPE:
            return {
                ...state,
                todayDate: new Date(),
                calDateLevelType: action.payload,
            };

        default:
            return {
                ...state,
                 
            };
    }
    
  }
  
  export default flexCalReducer;

