import './css/FlexCalendar.css';
import React, { useContext, useRef } from 'react';
import {useState, useEffect} from 'react';
import { dateHelper } from './model/CalDateHelper';
import { useDispatch, useSelector } from 'react-redux';
import { curDateSelector } from './redux/FlexCalSelectors';
import FlexCalYearMonthView from './FlexCalYearMonthView';
import { actionGoNextYear, actionGoPrevYear } from './redux/FlexCalActionCreators';
import { CalDBContext } from '../App';
import { CalYearDayHasData } from './entity/ICalYearDayVM';
import { PrevNextView } from './view/PrevNextView';
import { useContainerDimensions } from './hooks/useContainerDimensions';
import { getResponsiveTextSize, getResponsiveTitleSize } from './responsive/responsive';

export default function FlexCalYearViewContainer(props: any) {

  const flexCalDBHelper = useContext(CalDBContext);

  const componentRef = useRef<any>();
  const { width, height } = useContainerDimensions(componentRef);
    

    const dispatch = useDispatch();
    const curDate = useSelector(curDateSelector);
    const months = dateHelper.getAllMonthsForYear(curDate);

    const [ yearItems, setYearItems] = useState<CalYearDayHasData>( );

    useEffect(()=>{

        console.log(`year_items::curDate == ${curDate}...months==${months}...`);

      (async ()=> {

        const db_year_items = await flexCalDBHelper.getTextEventsByYear(curDate);
        setYearItems(db_year_items);
 
        for (const [key, year_item] of Object.entries(db_year_items)) {
          console.log(`FlexCalYearMonthView...year_items::key==${key}...year_item == ${year_item}`);
          console.log(`FlexCalYearMonthView...year_items::test[${key}]==${(db_year_items[key]==true)} `);
    
        }
 
        

      })();
      

    }, []);

    function doPrev() {
      console.log('do prev...');
      dispatch(actionGoPrevYear());
    }

    function doNext() {
      console.log('do next...');
      dispatch(actionGoNextYear());
    }

  return (

    <div ref={ componentRef } className="FlexCalFullYearContainer">

      <div className="FlexCalMonthHeader sticky-to-top-under ">

        <PrevNextView doPrev={ ()=>{ doPrev(); } } doNext={ ()=>{ doNext(); } }>
        
          <span style={{ fontSize: getResponsiveTitleSize(width) }}> { curDate.getFullYear() }   </span>

        </PrevNextView>
      
      </div>

      
        <br/>

        <div className="FlexCalYear"> 
        
        {
            months.map((date: Date) => <FlexCalYearMonthView 
                key={ dateHelper.getKeyFromDate(date) }
                monthDate={ date }
                yearItems={ yearItems || {} }
                ></FlexCalYearMonthView>)
        }
          
      </div>

    </div>
    
  );
}

 