export function getMonthContentCells(dispatch: any, date: Date, handleContentChangeEvent: (date: Date)=>void) {

    
    console.log(`FlexCalApp::getMonthContentCells...0...date==${date}`);

    const date_arr = dateHelper.arrayOfMonthDays(date);

    console.log(`FlexCalApp::getMonthContentCells...10...date_arr==${date_arr.length}`);

    const month_children =  date_arr.map((day_date)=>{

        //console.log(`FlexCalApp::getMonthContentCells...20...day_date==${day_date}`);

        return  (
            DayContentCells(dispatch,  day_date, handleContentChangeEvent)
        );
 
    });

    console.log(`FlexCalApp::getMonthContentCells...30...month_children==${month_children.length}`);

    return month_children;
}

export function DayContentCells(dispatch: any,  date: Date,
    handleContentChangeEvent: (date: Date)=>void) {
 

    //console.log(`FlexCalApp::getDayContentCells...0...date==${date}`);

    const onChildClick = (e: any, date: Date) => {
        //console.log(`FlexCalApp::getDayContentCells::onClick==... ...`);
      
        e.preventDefault();
        e.stopPropagation();

        dispatch(actionSetCurrentDate(date));
    }
 
    const onGoDailyViewClick =  (e: any, date: Date) => {
      
        console.log(`onGoDailyViewClick...date=${date}...`);

        e.preventDefault();
        e.stopPropagation();

        dispatch(actionGoDayOfDailyView(date));
    }
 
 
    const key = dateHelper.getKeyFromDate(date);
    
    return  (
        <CalendarMonthContentCell 
            /*onChildClick={ (e: any, date: Date)=>{ onChildClick(e, date);   } } */ 
            id={ "MC_" + dateHelper.getKeyFromDate(date) } 
            key={ dateHelper.getKeyFromDate(date) } 
            date= { date } 
            handleContentChangeEvent={ handleContentChangeEvent }
            >

            <button onClick={ (e: any)=>{ onGoDailyViewClick(e, date);} } className='btn-no-style' style={{ marginLeft: '6px', fontSize:'1.2rem', }}> {date.getDate()}  </button> 
        
        </CalendarMonthContentCell>
    );
            
    }
    

export function FlexCalApp() {

    console.log(`FlexCalApp...0...`);

    const dispatch = useDispatch();

    const componentRef = useRef<any>();
    const { width, height } = useContainerDimensions(componentRef);
    
    const calDateLevelType = useSelector(curCalDateLevelTypeSelector);
    const debugState = useSelector(debugSelector);
    
    const curDate = useSelector(curDateSelector);
    const curMonthViewModel = useSelector(curMonthViewModelSelector);
    const curWeekViewModels = useSelector(curWeekViewModelSelector);
 
    const yearmon: Date = useMemo(()=>{
        let yearmon_date = new Date(curDate);
        yearmon_date.setDate(1);
        return yearmon_date;
    }, [curDate]);

    console.log(`FlexCalApp::yearmon==${yearmon}...`);
     
    const handleContentChangeEvent = (date: Date)=>{

    };
     

 

    return (

            <div ref={ componentRef } className="FlexCalApp">
 
                <div className="center sticky-to-top  ">
                    <FlexCalDateLevelSwitch></FlexCalDateLevelSwitch>
                </div>

                <br />

            {
                (calDateLevelType==CalDateLevelTypeConstants.Year) && (
                    <FlexCalYearViewContainer></FlexCalYearViewContainer>
                )
            }
            
            {
            /*    
                (calDateLevelType==CalDateLevelTypeConstants.Month) && (yearmon) && (
                    <FlexCalMonthViewContainer>
                        { getMonthContentCells(dispatch, yearmon) }
                    </FlexCalMonthViewContainer>
                     
                   
                )
                */
                (calDateLevelType==CalDateLevelTypeConstants.Month) && (yearmon) && (
                    <FlexCalMonthViewContainer>
                       {
                        curMonthViewModel && curMonthViewModel.flexCalDayList.map((day_vm)=>{
                            return (
                                day_vm && DayContentCells(dispatch, day_vm.date, handleContentChangeEvent)
                            );
                        })
                    }
                    </FlexCalMonthViewContainer>
                     
                   
                )
                 
            }
            
            {
                (calDateLevelType==CalDateLevelTypeConstants.Week) && (
                    <FlexCalWeekViewContainer>
                    {
                        curWeekViewModels && curWeekViewModels.map((week_vm, index)=>{
                            return (
                                week_vm && DayContentCells(dispatch, week_vm.date, handleContentChangeEvent)
                            );
                        })
                    }
                    </FlexCalWeekViewContainer>
                )
            }
            {
                (calDateLevelType==CalDateLevelTypeConstants.Day) && (
                    <FlexCalDayViewContainer>
                        { 
                        <CalendarMonthContentCell  
                            id={ "MC_" + dateHelper.getKeyFromDate(curDate) } 
                            key={ dateHelper.getKeyFromDate(curDate) } date= { curDate }
                            handleContentChangeEvent={ handleContentChangeEvent }>
                             <div></div>
                        </CalendarMonthContentCell>
                        }
                    </FlexCalDayViewContainer>
                )
            }

            </div>

         
         
         
    );
}

 
