
import * as actionTypes from "./FlexCalActionTypes"
import { CalDateLevelTypeConstants, FlexCalLangChoice } from '../model/FlexCalConstants';
import FlexCalDayViewModel from '../model/FlexCalDayViewModel';
 
export function actionSetLanguage(langChoice: FlexCalLangChoice) {

    return {
      type: actionTypes.ACTION_SET_LANGUAGE,
      payload: langChoice
    };
  }

  export function actionSetCalViewType(calViewType: CalDateLevelTypeConstants) {

    return {
      type: actionTypes.ACTION_SET_CAL_VIEW_TYPE,
      payload: calViewType
    };
  }

export function actionGoPrevMonth() {
    return {
      type: actionTypes.ACTION_GO_PREV_MONTH
    }
  }

export function actionGoNextMonth() {

  return {
    type: actionTypes.ACTION_GO_NEXT_MONTH
  }
}

export function actionGoPrevYear() {
  return {
    type: actionTypes.ACTION_GO_PREV_YEAR
  }
}

export function actionGoNextYear() {

  return {
    type: actionTypes.ACTION_GO_NEXT_YEAR
  }
}

export function actionGoDayOfYear(dateVM: FlexCalDayViewModel) {

  return {
    type: actionTypes.ACTION_GO_DAY_OF_YEAR,
    payload: dateVM,
  }
}

export function actionGoDayOfDailyView(date: Date) {

  return {
    type: actionTypes.ACTION_GO_DAY_OF_DAILY_VIEW,
    payload: date,
  }
}

export function actionSetCurrentDate(date: Date) {

  return {
    type: actionTypes.ACTION_SET_CURRENT_DATE,
    payload: date,
  }
}

 
 

export function actionGoPrevWeek() {
  return {
    type: actionTypes.ACTION_GO_PREV_WEEK
  }
}

export function actionGoNextWeek() {

  return {
    type: actionTypes.ACTION_GO_NEXT_WEEK
  }
}


export function actionGoPrevDay() {
  return {
    type: actionTypes.ACTION_GO_PREV_DAY
  }
}

export function actionGoNextDay() {

  return {
    type: actionTypes.ACTION_GO_NEXT_DAY
  }
}