import './css/FlexCalendar.css';
import { CalDateLevelTypeConstants } from './model/FlexCalConstants';
import { useDispatch, useSelector } from 'react-redux';
import { actionSetCalViewType } from './redux/FlexCalActionCreators';
import { stateSelector, stringSelector } from './redux/FlexCalSelectors';
import { useRef } from 'react';
import { useContainerDimensions } from './hooks/useContainerDimensions';
import {getResponsiveTextSize} from './responsive/responsive';

export default function FlexCalDateLevelSwitch() {

  const dispatch = useDispatch();
  const strings = useSelector(stringSelector);
  
  const componentRef = useRef<any>();
  const { width, height } = useContainerDimensions(componentRef);


  function doSetDateLevelYear() {
    console.log('doSetDateLevelYear...');
    dispatch(actionSetCalViewType(CalDateLevelTypeConstants.Year));
  }
  function doSetDateLevelMonth() {
    console.log('doSetDateLevelMonth...');
    dispatch(actionSetCalViewType(CalDateLevelTypeConstants.Month));
  }
  function doSetDateLevelWeek() {
    console.log('doSetDateLevelWeek...');
    dispatch(actionSetCalViewType(CalDateLevelTypeConstants.Week));
  }
  function doSetDateLevelDay() {
    console.log('doSetDateLevelDay...');
    dispatch(actionSetCalViewType(CalDateLevelTypeConstants.Day));
  }
  

  return (

    <> 
      {
        componentRef && <div ref={componentRef} className="FlexCalHeader center" >
            
        <button onClick={doSetDateLevelDay}>
          <span style={{ fontSize: getResponsiveTextSize(width) }} >{ strings.day }</span> 
        </button>
        <button onClick={doSetDateLevelWeek}>
        <span style={{ fontSize: getResponsiveTextSize(width) }} >{ strings.week }</span> 
        </button>
        <button onClick={doSetDateLevelMonth}>
        <span style={{ fontSize: getResponsiveTextSize(width) }} >{ strings.month }</span> 
        </button>
        <button onClick={doSetDateLevelYear}>
        <span style={{ fontSize: getResponsiveTextSize(width) }} >{ strings.year }</span> 
        </button>

        </div>
      }
             
 

    </>
  );
}

 