import { useRef, useState } from "react";
import './css/TabPanel.css';
import '../../css/FlexCalendar.css';
import { ITabModel } from "./model/ITabModel";
import { useContainerDimensions } from "../../hooks/useContainerDimensions";
import { getResponsiveTitleSize } from '../../responsive/responsive';

export default function TabContainer({tabModels, children}: {tabModels: ITabModel[], children: any[]}) {

    const [selectedTab, setSelectedTab ] = useState<number>(0);

    const componentRef = useRef<any>();
    const { width, height } = useContainerDimensions(componentRef);
    

    return (
        <div ref={ componentRef } className="tab-panel-container">
            
            <div>
            {
                tabModels.map((tabModel, index)=>{
                    return (<button key={index} 
                        className={ (selectedTab == index) ? 'selected-tab btn-no-style' : 'btn-no-style'}
                        onClick={ ()=>setSelectedTab(index) }>
                         
                            <span style={{ fontSize: getResponsiveTitleSize(width) }}>{ tabModel.tabHeader }</span> 
                        
                    </button>);
                })
            }
            </div>
            
            {
                tabModels.map((tabModel, index)=>{
                    return (
                        <div key={index} className="tab-panel-content">
                            { (selectedTab == index) && children[index] }
                        </div>
                    );
                })
            }
        </div>
    );
}