import '../css/FlexCalendar.css';
import { useCallback, useContext, useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useContainerDimensions } from "../hooks/useContainerDimensions";
import { curDateSelector, curWeekViewModelSelector, stringSelector } from "../redux/FlexCalSelectors";
import FlexCalDayViewModel from '../model/FlexCalDayViewModel';
import { actionSetCurrentDate } from '../redux/FlexCalActionCreators';
import { CalDBContext } from "../../App";
import { CalYearDayHasData } from "../entity/ICalYearDayVM";
import { dateHelper } from "../model/CalDateHelper";


export function FlexCalWeekControl({handleWeekItemClickEvent}:{handleWeekItemClickEvent: (dayVM: FlexCalDayViewModel)=>void}) {

    const dispatch = useDispatch();
    const strings = useSelector(stringSelector);
    const curDate = useSelector(curDateSelector);
    const weekState = useSelector(curWeekViewModelSelector);
    const flexCalDBHelper = useContext(CalDBContext);
    
    const weekLabels = [
        strings.sun,
        strings.mon,
        strings.tue,
        strings.wed,
        strings.thu,
        strings.fri,
        strings.sat,
    ];

     //
     const [ weekItems, setWeekItems] = useState<CalYearDayHasData>( );
     const updateWeeklyItems = useCallback(async ()=> {
     
         console.log(`updateWeeklyItems...0...`);
         
         const week_date = weekState && weekState.map((week)=>week.date);
         const min_week_date = (week_date && week_date.length > 0) ? week_date[0] : undefined;
         const max_week_date = (week_date && week_date.length > 0) ? week_date[week_date.length-1] : undefined
 
         console.log(`updateWeeklyItems...0...min_week_date==${min_week_date}...max_week_date==${max_week_date}...`);

         if (min_week_date && max_week_date) {
           const db_week_items = await flexCalDBHelper.getTextEventsByDateRange(min_week_date, max_week_date );
           setWeekItems(db_week_items);  

           console.log(`updateWeeklyItems...20...weekItems==${JSON.stringify(weekItems)}...`);
         }
 
   }, [curDate]);

   //
   useEffect(()=>{
    updateWeeklyItems();
   }, [curDate]);
   

   useEffect(()=>{

    console.log(`year_items::curDate == ${curDate}...`);

    const dbChangeEventListener = {
        onDbContentChangedEventOccurred: (date: Date)=>{

            console.log(`FlexCalMonthViewHeaderBig::useEffect...0...::registerDbChangeEvent...`);
            (async ()=> {
                await updateWeeklyItems();
             })();
        }
    };

    //
    flexCalDBHelper.registerDbChangeEvent( dbChangeEventListener );

    return () => flexCalDBHelper.unregisterDbChangeEvent(dbChangeEventListener);

});


    function onClickWeekItem(e: any, dayVM: FlexCalDayViewModel) {
      dispatch(actionSetCurrentDate(dayVM.date));
      handleWeekItemClickEvent(dayVM);
    }
 

    return (<div className="center sticky-to-top-under" style={{ textAlign: 'center' }} >
        { 
        weekState && weekState.map((dayVM, index) => {

            return (

                <a key={ 'WC_' + dayVM.date.getDate() }  href={'#D' + dayVM.date.getDate()} > 
                {
                   
                   <button  onClick={ (e:any)=>onClickWeekItem(e, dayVM) } 
                      className="btn-no-styleFlexCalSmallDayControlBtn">
                        <span className={`${(weekItems && (weekItems[dateHelper.getKeyFromDate(dayVM.date)] ==true)) ? " year-has-data-hint ": "  "}`} >
                        { weekLabels[dayVM.date.getDay()].charAt(0)  }
                        </span>
                         
                      </button>
                     
                }
                      
                </a>

              );
        })
      }
        
        </div>);
}