
 

    export function getResponsiveContainerWidth(width: number) {
        
        const font_size = '2048px';
        if (width > 1024) {
            return '60%';
        }

        else if (width > 1024) {
            return '80%';
        }

        else if (width > 720) {
            return '90%';
        }

        else {
            return '100%';
        }

    }

    export function getResponsiveTextSize(width: number) {


        const font_size = '20px';
        if (width > 1024) {
            return '26px';
        }

        else if (width > 720) {
            return '24px';
        }

        else if (width > 360) {
            return '20px';
        }

        else {
            return '16px';
        }

    }


    export function getResponsiveTitleSize(width: number) {


        const font_size = '20px';
        if (width > 1024) {
            return '20px';
        }

        else if (width > 720) {
            return '18px';
        }

        else if (width > 360) {
            return '16px';
        }

        else {
            return '14px';
        }

    }


