import { FileInfo } from "./FileInfo";
import { CalEventDataType, ICalEventData } from "./ICalEventData";

export class CalEventDataWrapper {

    calEventData: ICalEventData;

    constructor(calEventData: ICalEventData) {
        this.calEventData = calEventData;
    }

    getFileInfo()  {

        if (this.calEventData && this.calEventData.eventFile) {

            const fileInfo = new FileInfo();
       
            fileInfo.file = this.calEventData.eventFile;
            fileInfo.fileName = this.calEventData.eventFilename;
            fileInfo.fileType = this.calEventData.fileType;
            fileInfo.fileSize = this.calEventData.fileSize;
            fileInfo.fileLastModified = this.calEventData.fileLastModified;
         
    
            return fileInfo;
        }
    }
     
     

  }
  