import './css/FlexCalendar.css';
import React, { useContext, useRef } from 'react';
import {useState, useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from './model/CalDateHelper';
import { stringSelector, curDateSelector, stateSelector, curMonthViewModelSelector, curWeekViewModelSelector } from './redux/FlexCalSelectors';
import { actionGoNextDay, actionGoNextMonth, actionGoNextWeek, actionGoPrevDay, actionGoPrevMonth, actionGoPrevWeek } from './redux/FlexCalActionCreators';
import FlexCalMonthViewModel from './model/FlexCalMonthViewModel';
import { PrevNextView } from './view/PrevNextView';
import { useContainerDimensions } from './hooks/useContainerDimensions';
import { getResponsiveTextSize, getResponsiveTitleSize } from './responsive/responsive';

export default function FlexCalDayViewContainer(props: any) {

  const componentRef = useRef<any>();
  const { width, height } = useContainerDimensions(componentRef);
   
  //
  const dispatch = useDispatch();
  const strings = useSelector(stringSelector);
  const curDate = useSelector(curDateSelector);
  const weekState = useSelector(curWeekViewModelSelector);
  const weekLabels = [
      strings.sun,
      strings.mon,
      strings.tue,
      strings.wed,
      strings.thu,
      strings.fri,
      strings.sat,
  ];
  const monthLabels = [
    strings.jan,
    strings.feb,
    strings.mar,
    strings.apr,
    strings.may,
    strings.jun,
    strings.jul,
    strings.aug,
    strings.sep,
    strings.oct,
    strings.nov,
    strings.dec,
];

    

  function doPrev() {
    console.log('do prev...');
    dispatch(actionGoPrevDay());
  
  }
  function doNext() {
    console.log('do next...');
    dispatch(actionGoNextDay());
  
  }

  return (<> 

    <div ref={componentRef}  className='FlexCalFullDayContainer'> 
 
      <div className="FlexCalFullDayHeader  sticky-to-top-under">

             
            <PrevNextView doPrev={ ()=>{ doPrev(); } } doNext={ ()=>{ doNext(); } }>
 
              <span style={{ fontSize: getResponsiveTitleSize(width) }}>
                { curDate.getDate() }
                &nbsp;
                { monthLabels[ curDate.getMonth() ] }  
                &nbsp;
                { curDate.getFullYear() }
              </span>
 
            </PrevNextView>

             
      </div>

      <br/>

      <div className="FlexCalFullDayBody border"> 
   
          { props.children }

      </div>

    </div>

  </>);
}


 