import '../css/FlexCalendar.css';
import React, { useContext, useRef } from 'react';
import { useState, useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from '../model/CalDateHelper';
import { stringSelector, curDateSelector, stateSelector, curMonthViewModelSelector, todayDateSelector } from '../redux/FlexCalSelectors';
import { actionGoNextMonth, actionGoPrevMonth, actionSetCurrentDate } from '../redux/FlexCalActionCreators';
import FlexCalMonthViewModel from '../model/FlexCalMonthViewModel';
import CalendarMonthContentCell from '../external/CalendarMonthContentCell';
import { PrevNextView } from '../view/PrevNextView';
import { TestCalMonthContentCell } from '../external/TestCalMonthContentCell';
import { useContainerDimensions } from '../hooks/useContainerDimensions';
import { getResponsiveTextSize, getResponsiveTitleSize } from '../responsive/responsive';
import FlexCalYearMonthView from '../FlexCalYearMonthView';
import { CalDBContext } from '../../App';
import { CalYearDayHasData } from '../entity/ICalYearDayVM';
import FlexCalYearMonthControl from './FlexCalYearMonthControl';
 

export default function FlexCalMonthViewHeaderBig(props: {width: number}) {

    const dispatch = useDispatch();
    const strings = useSelector(stringSelector);
    const curDate = useSelector(curDateSelector);
    const flexCalDBHelper = useContext(CalDBContext);
    
    

    const monthLabels = [
        strings.jan,
        strings.feb,
        strings.mar,
        strings.apr,
        strings.may,
        strings.jun,
        strings.jul,
        strings.aug,
        strings.sep,
        strings.oct,
        strings.nov,
        strings.dec,
    ];

    const [ yearItems, setYearItems] = useState<CalYearDayHasData>( );

    const updateYearlyItems = useCallback(async ()=> {
    
        console.log(`updateYearlyItems...0...`);

        const db_year_items = await flexCalDBHelper.getTextEventsByYearMonth(curDate);
        setYearItems(db_year_items);


        for (const [key, year_item] of Object.entries(db_year_items)) {
            console.log(`db_year_items::key==${key}...year_item == ${year_item}`);
            console.log(`db_year_items::test[${key}]==${(db_year_items[key]==true)} `);
        }

        /*
        db_year_items.forEach((year_item, key)=>{
            console.log(`db_year_items::key==${key}...year_item == ${year_item}`);
            console.log(`db_year_items::test[${key}]==${(db_year_items.get(key)==true)} `);
        });
        */

  }, [curDate]);

    useEffect(()=>{

        console.log(`year_items::curDate == ${curDate}...`);
 
        (async ()=> {
           await updateYearlyItems();
        })();
        
    }, [curDate]);


    useEffect(()=>{

        console.log(`year_items::curDate == ${curDate}...`);
 
        const dbChangeEventListener = {
            onDbContentChangedEventOccurred: (date: Date)=>{

                console.log(`FlexCalMonthViewHeaderBig::useEffect...0...::registerDbChangeEvent...`);
                (async ()=> {
                    await updateYearlyItems();
                 })();
            }
        };

        //
        flexCalDBHelper.registerDbChangeEvent( dbChangeEventListener );
 
        return () => flexCalDBHelper.unregisterDbChangeEvent(dbChangeEventListener);
 
    });


    function doPrev() {
        console.log('do prev...');
        dispatch(actionGoPrevMonth());

    }
    function doNext() {
        console.log('do next...');
        dispatch(actionGoNextMonth());

    }

    return (

            <div className="FlexCalMonthHeader  sticky-to-top-under ">

                <PrevNextView doPrev={() => { doPrev(); }} doNext={() => { doNext(); }}>

                    <div className='FlexCalShowWhenBigControl' style={{ fontSize: getResponsiveTitleSize(props.width) }}>
                        {monthLabels[curDate.getMonth()]}
                        &nbsp;
                        {curDate.getFullYear()}  
                    
                    </div>

                    <div className="FlexCalHideWhenBigControl" > 
                    <div className="FlexCalYear" style={{ width:'200px', margin:'auto' }}> 
                    {
                       
                        curDate && <FlexCalYearMonthControl 
                            key={ dateHelper.getKeyFromDate(curDate) }
                            monthDate={ curDate }
                            yearItems={ yearItems || {} } ></FlexCalYearMonthControl>
                         
                    }
                    </div>
                    </div>
                    

                </PrevNextView>


            </div>

  

    );
}


