import { createRef, RefObject, useRef, useState, useEffect } from "react";
import { CalEventDataType, ICalEventData } from "../entity/ICalEventData";
import TabContainer from "./TabPanel/TabContainer";
import CommonHelper from '../model/CommonHelper';
import './css/CalendarExternal.css';
import './TabPanel/css/TabPanel.css';
import { DropZoneComp } from "../DragAndDrop/DropZoneComp";
import { FileInfo } from "../entity/FileInfo";
import { MediaRenderView } from "../view/MediaRenderView";
import { useContainerDimensions } from "../hooks/useContainerDimensions";
import {getResponsiveTitleSize} from '../responsive/responsive';
 

export default function CalendarInputForm({date, saveEventData} : {date: Date, saveEventData: (data: ICalEventData)=>void}) {

    
    ////const textRef: RefObject<HTMLInputElement> = createRef();
    
    const [text, setText] = useState('');
    const [fileInfo, setFileInfo] = useState<FileInfo>();
    const textAreaRef = useRef<any>();
    const saveBtnRef = useRef<any>();

    useEffect(()=>{
        textAreaRef.current.focus();
    }, []);

 

    function handleChangeText(e: any) {
        setText(e.target.value);
    }

    function handleChangeFile(file: File) {

        console.log(`handleChangeFile...0...${file.size}...`);

        const fileInfo = new FileInfo();

        fileInfo.file = file;
        fileInfo.fileName = file.name;
        fileInfo.fileType = file.type;
        fileInfo.fileSize = file.size;
        fileInfo.fileLastModified = file.lastModified;

        setFileInfo(fileInfo);

       
      }

    return (<>
  
        <TabContainer
            tabModels={ [{tabHeader: 'Enter Memo'}] }>
 
   
   
            <div  className="input-form-outer" >
                 
            

                <div className="input-form-header " >

                    <span> </span>

                    {
                    
                        <button ref={ saveBtnRef } className="action-button"  onClick={ ()=>((text && text.length > 0) || fileInfo?.file) && saveEventData({
                            eventId: CommonHelper.generateUUIDKey(),
                            eventDate: date,
                            eventDataType: CalEventDataType.message,
                        
                            eventText: text || '', 
                            eventFile: fileInfo?.file ? fileInfo?.file : undefined,
                            eventFilename: fileInfo?.fileName,
                            fileType: fileInfo?.fileType,
                            fileSize: fileInfo?.fileSize,
                            fileLastModified: fileInfo?.fileLastModified,

                            eventCategoryId: 0,
                            eventTags: '',
                            
                            updateUserId: 0,
                            updateDate: (new Date()) 
                        }) } >
                            <span>Save</span> 
                        </button>
                
                    }
                </div>


                <div>
                    <textarea ref={ textAreaRef }  style={{ width: '100%', height: '100px', outline: 'none'}}  
                        onChange={ handleChangeText } 
                        value={ text }  placeholder='Enter Text'  /> 
                </div>
                 
                <div>
                    <br />              
                </div>

                <div style={{ height: '80%', overflow: 'auto'}}>
                {
 
                    <DropZoneComp 
                        onHandleDrop={ handleChangeFile } accept={"images/png"} ></DropZoneComp>
                    }

                </div>
                
                
            </div>

          

            <div></div>

             
             
     
      
 
    </TabContainer>
   
 
        
    
 
         

        
        
    </>);
}