import React, { useCallback, useState, useEffect } from "react";
import { Accept, useDropzone } from "react-dropzone";
import { reduce } from "underscore";
import { FileInfo } from "../entity/FileInfo";
import { MediaRenderView } from "../view/MediaRenderView";
import './DropZoneComp.css';
 

export function DropZoneComp({ onHandleDrop, accept }: {
    onHandleDrop: (file: any) => void,
    accept: any
}) {

    const [fileInfo, setFileInfo] = useState<FileInfo>();

    ////const [objUrl, setObjUrl] = useState<any>();

    const onDrop: (acceptedFiles: any) => void = useCallback(acceptedFiles => {
        // this callback will be called after files get dropped, we will get the acceptedFiles. If you want, you can even access the rejected files too
        console.log(`acceptedFiles==${JSON.stringify(acceptedFiles)}...`);
        updateMediaPreview(acceptedFiles[0]);
        onHandleDrop(acceptedFiles[0]);
    }, []);

    function updateMediaPreview(file: File) {
        
        const fileInfo = new FileInfo();

        fileInfo.file = file;
        fileInfo.fileName = file.name;
        fileInfo.fileType = file.type;
        fileInfo.fileSize = file.size;
        fileInfo.fileLastModified = file.lastModified;

        setFileInfo(fileInfo);

    }
 

    const { getRootProps, getInputProps, isDragActive } = useDropzone({
        onDrop,
        accept
    });

    return (
        <>
     
 
            <div className="dropzone-div" {...getRootProps()}>
                <input className="dropzone-input" {...getInputProps()} />
                <div className="text-center">
                    {isDragActive ? (
                        <p className="dropzone-content">Release to drop the files here</p>
                    ) : (
                        <p className="dropzone-content">
                            Drag 'n' drop image / mp4 / mp3, or Click here to select

                        </p>
                    )}
                </div>
                
                {
                /*
                <div style={{ height: '100%', overflow: 'auto' }}>
                    {
                        objUrl && (<img className="image-preview" src={URL.createObjectURL(objUrl)}/>)
                    }
                </div>
                */
                }
                 <div style={{ height: '100%', overflow: 'auto' }}> 
                {
                fileInfo && <MediaRenderView fileInfo={ fileInfo }></MediaRenderView>
                }
                </div>
              
            </div>
        </>
    );
}
