

export class FileInfo {
    file?: File;
    fileName?: string;
    fileType?: string;
    fileSize?: number;
    fileLastModified?: number;

    getFileExtension() {
        return this.fileName?.substr(this.fileName?.lastIndexOf('.') + 1)?.toLowerCase()?.trim();
    }
    hasImage() {
        const ext = this.getFileExtension();
        return ext ? ['png', 'jpg', 'jpeg', 'gif', 'bmp', 'tiff', 'tif'].includes(ext) : false;
    }
    hasVideo() {
        const ext = this.getFileExtension();
        return ext ? ['mp4', 'avi'].includes(ext) : false;
    }
    hasAudio() {
        const ext = this.getFileExtension();
        return ext ? ['mp3'].includes(ext) : false;
    }
}