import { isConstructorDeclaration } from "typescript";


export enum CalDateLevelTypeConstants {

    Year = "YEAR",
    Month = "MONTH",
    Week = "WEEK",
    Day = "DAY"
};

export enum FlexCalLangChoice {
    En = "En",
    Zh = "Zh"
}

export function flexCalLangChoiceFromString(code: string | null): FlexCalLangChoice {
    switch (code) {
        case "Zh":
            return FlexCalLangChoice.Zh;
        default:
            return FlexCalLangChoice.En;
    }
}



export const KEY_LANGUAGE_CHOICE: string = "KEY_LANGUAGE_CHOICE";