
import { findAllByTestId } from "@testing-library/react";
import _ from "underscore";
import FlexCalDayViewModel from "./FlexCalDayViewModel";
import FlexCalMonthViewModel from "./FlexCalMonthViewModel";

 

class DateHelper {

    /*
    const sorted_date_list = date_list.sort((d1, d2)=>{
             if (d1.getTime() < d2.getTime()) {
                 return -1;
             }
             else if (d1.getTime() > d2.getTime()) {
                return 1;
            }
            else {
                return 0;
            }
        });
    */
    isDateInList(date: Date, sorted_date_list: Date[]) {
 
        const min_date: Date = new Date(sorted_date_list[0]);
        const max_date: Date = new Date(sorted_date_list[sorted_date_list.length-1]);
 
        min_date.setHours(0);
        min_date.setMinutes(0);
        min_date.setSeconds(0);
        min_date.setMilliseconds(0);
        max_date.setHours(23);
        max_date.setMinutes(59);
        max_date.setSeconds(59);
        max_date.setMilliseconds(999);

        return (date >= min_date && date <= max_date);
    }

    arrayOfMonthDays(date: Date) {
        //console.log(`arrayOfMonthDays...0...${date}...`);
        let results: Date[] = [];
        for (let i=1; i <= this.getLastDateOfMonth(date).getDate(); i++) {
            //console.log(`arrayOfMonthDays...10...${i}...`);
            let new_date=new Date(date);
            new_date.setDate(i);
            results.push(new_date);
        }
        //console.log(`arrayOfMonthDays...20...${results.length}...`);
        return results
    }

    getKeyFromDate(date: Date) {
        return `${date.getFullYear()}-${1 + date.getMonth()}-${date.getDate()}`;
    }

    isSameDate(date1: Date, date2: Date) {

        //console.log(`isSameDate...${date1} VS...${date2}...`);

        return (date1.getFullYear() === date2.getFullYear()) &&
            (date1.getMonth() === date2.getMonth()) &&
            (date1.getDate() === date2.getDate());
    }
    isSameMonth(date1: Date, date2: Date) {
        return (date1.getFullYear() === date2.getFullYear()) &&
            (date1.getMonth() === date2.getMonth());
    }

    getMonthCalendarViewModel(date: Date): FlexCalMonthViewModel {
 
        let firstDayInMonth = this.getWeekDayOfFirstDay(date);
        let numOfDaysOfMonth = this.getNumOfDaysOfMonth(date);
        let lastDateOfPreviousMonth = this.getLastDateOfPreviousMonth(date);
        let firstDateOfCurrentMonth = this.getFirstDateOfMonth(date);
        let firstDateOfNextMonth = this.getFirstDateOfNextMonth(date);
        
        console.log(`firstDayInMonth==${firstDayInMonth}`);
        console.log(`numOfDaysOfMonth==${numOfDaysOfMonth}`);
        console.log(`lastDateOfPreviousMonth==${lastDateOfPreviousMonth}...${lastDateOfPreviousMonth.getDay()}`);
        console.log(`firstDateOfCurrentMonth==${firstDateOfCurrentMonth}`);
        console.log(`firstDateOfNextMonth==${firstDateOfNextMonth}`);

        const numOfShowPrevMonthDays = (1 + lastDateOfPreviousMonth.getDay()) % 7;
        const numOfShowNextMonthDays = (7 - firstDateOfNextMonth.getDay()) % 7;

        console.log(`numOfShowPrevMonthDays==${numOfShowPrevMonthDays}`);
        console.log(`numOfShowNextMonthDays==${numOfShowNextMonthDays}`);

        //
        const calMonthVM = new FlexCalMonthViewModel();
         
        for (let pre_index=0; pre_index < numOfShowPrevMonthDays; pre_index++) {

            let date_obj = new Date(
                lastDateOfPreviousMonth.getFullYear(), 
                lastDateOfPreviousMonth.getMonth(),
                (lastDateOfPreviousMonth.getDate() - pre_index));
            
            let calDayViewModel = new FlexCalDayViewModel();
            calDayViewModel.setDate(date_obj, date);

            calMonthVM.flexCalDayList = [calDayViewModel, ...calMonthVM.flexCalDayList];
        }

        for (let cur_index=0; cur_index < numOfDaysOfMonth; cur_index++) {
            
            let date_obj = new Date(
                date.getFullYear(), 
                date.getMonth(),
                (cur_index + 1));
            
            let calDayViewModel = new FlexCalDayViewModel();
            calDayViewModel.setDate(date_obj, date);
    
            calMonthVM.flexCalDayList = [...calMonthVM.flexCalDayList, calDayViewModel];
        }

        for (let next_index=0; next_index < numOfShowNextMonthDays; next_index++) {
            
            let date_obj = new Date(
                firstDateOfNextMonth.getFullYear(), 
                firstDateOfNextMonth.getMonth(),
                (firstDateOfNextMonth.getDate() + next_index));
            
            let calDayViewModel = new FlexCalDayViewModel();
            calDayViewModel.setDate(date_obj, date);
    
            calMonthVM.flexCalDayList = [...calMonthVM.flexCalDayList, calDayViewModel];
        }

        return calMonthVM;
    }

    getAllMonthsForYear(date: Date) {

        const result: Date[] = [];

        for (let month=0; month < 12; month++) {
            result.push(new Date(date.getFullYear(), month, 1));
        }

        return result;
    }

    getNumOfDaysOfMonth(date: Date) {
        let month = date.getMonth() + 1;
        let year = date.getFullYear();
        let daysInMonth = new Date(year, month, 0).getDate();

        return daysInMonth;
    }


     

    getWeekDayOfFirstDay(date: Date) {
        let month = date.getMonth();
        let year = date.getFullYear();

        let firstDayInMonth = new Date(year, month, 1).getDay();
        return firstDayInMonth;
    }

    getDateOfPreviousDay(date: Date): Date {

        let prev_day_date = new Date(date);
        prev_day_date.setDate( date.getDate() - 1 );
        return prev_day_date;
    }

    getDateOfNextDay(date: Date): Date {

        let next_day_date = new Date(date);
        next_day_date.setDate( date.getDate() + 1 );
        return next_day_date;
    }

    getDateOfPreviousWeek(date: Date): Date {

        let prev_week_last_date = new Date(date);
        prev_week_last_date.setDate( date.getDate() - 7 );
        return prev_week_last_date;
    }

    getDateOfNextWeek(date: Date): Date {

        let prev_next_last_date = new Date(date);
        prev_next_last_date.setDate( date.getDate() + 7 );
        return prev_next_last_date;
    }

    getLastDateOfPreviousMonth(date: Date): Date {

        let prev_month_last_date = new Date(date);
        prev_month_last_date.setDate(1);
        prev_month_last_date.setHours(-1);
        return prev_month_last_date;
    }

     
    getLastDateOfMonth(date: Date): Date {

        let next_month_first_date = new Date(date);
        next_month_first_date.setDate(1);
        next_month_first_date.setMonth(1 + date.getMonth());
        next_month_first_date.setHours(-1);
        return next_month_first_date;
    }

    getFirstDateOfPreviousMonth(date: Date): Date {

        let prev_month_last_date = new Date(date);
        prev_month_last_date.setDate(1);
        prev_month_last_date.setHours(-1);
 
        return new Date(prev_month_last_date.getFullYear(),
            prev_month_last_date.getMonth(),
            1
        );
    }

    getFirstDateOfMonth(date: Date): Date {

        let cur_month_first_date = new Date(date);
        cur_month_first_date.setDate(1);
        cur_month_first_date.setHours(+1);
        return cur_month_first_date;
    }

    getFirstDateOfNextMonth(date: Date): Date {

        let next_month_first_date = new Date(date);
        next_month_first_date.setDate(1);
        next_month_first_date.setMonth(1 + date.getMonth());
        next_month_first_date.setHours(+1);
        return next_month_first_date;
    }

    getPreviousYear(date: Date): Date {

        let prev_year_last_date = new Date(date);
        prev_year_last_date.setFullYear(date.getFullYear() - 1);
     
        return prev_year_last_date;
    }

    getNextYear(date: Date): Date {

        let next_year_last_date = new Date(date);
        next_year_last_date.setFullYear(date.getFullYear() + 1);
     
        return next_year_last_date;
    }

    getWeeksLabel(): string[] {
        return ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
    }

}


export const dateHelper = new DateHelper(); 