
import { createStore, applyMiddleware, Store } from "redux"
import thunk from "redux-thunk"
import IFlexCalState from "../model/IFlexCalState"
import { CalDateLevelTypeConstants, FlexCalLangChoice, flexCalLangChoiceFromString, KEY_LANGUAGE_CHOICE } from '../model/FlexCalConstants';
import { FlexCalAction } from './FlexCalActionTypes';
import flexCalReducer from "./FlexCalReducer"
import { localStorageHelper } from "../model/LocalStorageHelper";
import { dateHelper } from '../model/CalDateHelper';
import StringZh from '../strings/string-zh';
import StringEn from "../strings/string-en";
import { actionSetCurrentDate } from "./FlexCalActionCreators";


export const initialState: IFlexCalState = {
    strings: localStorageHelper.getValue(KEY_LANGUAGE_CHOICE) == FlexCalLangChoice.Zh ? new StringZh().constants : new StringEn().constants,
    curDate: new Date(),
    todayDate: new Date(),
    calDateLevelType: CalDateLevelTypeConstants.Month,
    curLang: flexCalLangChoiceFromString(localStorageHelper.getValue(KEY_LANGUAGE_CHOICE)) ?? FlexCalLangChoice.En,
    monthViewModel: dateHelper.getMonthCalendarViewModel(new Date),

    lastContentChangedDate: undefined,
}

export const flexCalStore: Store<IFlexCalState, FlexCalAction> = createStore(flexCalReducer, applyMiddleware(thunk))
 