import './css/FlexCalendar.css';
import React, { useContext } from 'react';
import {useState, useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from './model/CalDateHelper';
import { stringSelector, curDateSelector, stateSelector, curMonthViewModelSelector } from './redux/FlexCalSelectors';
import { actionGoDayOfYear, actionGoNextMonth, actionGoPrevMonth } from './redux/FlexCalActionCreators';
import FlexCalMonthViewModel from './model/FlexCalMonthViewModel';
import FlexCalDayViewModel from './model/FlexCalDayViewModel';
import { bool } from 'yup';
import { CalYearDayHasData } from './entity/ICalYearDayVM';

export default function FlexCalYearMonthView({monthDate, yearItems}: {monthDate: Date,  yearItems: CalYearDayHasData}) {

  console.log(`FlexCalYearMonthView...0...${monthDate}...${yearItems.size}...`);

  //
  const dispatch = useDispatch();
  const strings = useSelector(stringSelector);
 

  const weekLabels = [
      'S',
      'M',
      'T',
      'W',
      'T',
      'F',
      'S',
  ];

  const monthLabels = [
    strings.jan,
    strings.feb,
    strings.mar,
    strings.apr,
    strings.may,
    strings.jun,
    strings.jul,
    strings.aug,
    strings.sep,
    strings.oct,
    strings.nov,
    strings.dec,
];

   const calMonthViewModel: FlexCalMonthViewModel =  dateHelper.getMonthCalendarViewModel(monthDate);
   const weeks = calMonthViewModel.daysPartitionedByWeek;
   
   
  useEffect(()=>{

    for (const [key, year_item] of Object.entries(yearItems)) {
      console.log(`FlexCalYearMonthView...year_items::key==${key}...year_item == ${year_item}`);
      console.log(`FlexCalYearMonthView...year_items::test[${key}]==${(yearItems[key]==true)} `);

    }

    

    const today_key = dateHelper.getKeyFromDate(new Date());
    console.log(`FlexCalYearMonthView...today_key::test[${today_key}]==${(yearItems[today_key]==true)} `);


  }, []);


   function doSetDate(dateVM: FlexCalDayViewModel) {
    console.log('do doSetDate...');
    dispatch(actionGoDayOfYear(dateVM));
  }
    
  return (

    <div> 
  
      <div> { monthLabels[ monthDate.getMonth() ] } </div>
      <br/>

      <div className="FlexCalYearMonth"> 
 
      {
        weekLabels.map((label,index)=><div key={index} className="FlexCalYearMonthDaySlotHeader">{label}</div>)
      }

      { 
        weeks.map((week) => {
           
          return week.map((day) => {

            if (day.isCurrentMonth == true) {
              return (
  
                <div className="FlexCalYearMonthDaySlotCell enabled-style "
                  key={day.keyLabel}>
                    <button className={` btn-no-style : true  ${yearItems && (yearItems[dateHelper.getKeyFromDate(day.date)]==true) ? " year-has-data-hint ": "  "}`}  onClick={ ()=>{doSetDate(day)} }> {day.dayLabel} </button> 
                </div>
                  
              )
            }
            else {
              return (
  
                <div className="FlexCalYearMonthDaySlotCell disabed-style"
                  key={day.keyLabel}>
                    <button className='btn-no-style' onClick={ ()=>{doSetDate(day)} }>
                    
                      <span >{day.dayLabel}   </span>  
                      
                    </button> 
                </div>
                  
              )
            }
             
          })
          
        })
      }
      </div>


    </div>
  );
}

 