import { LegacyRef, useEffect, useRef } from 'react';
import { CalEventDataWrapper } from '../entity/CalEventData';
import { FileInfo } from '../entity/FileInfo';
import { CalEventDataType, ICalEventData } from '../entity/ICalEventData';
import { useContainerDimensions } from '../hooks/useContainerDimensions';
 


export function MediaRenderView({ fileInfo }: { fileInfo: FileInfo }) {
 
    const componentRef = useRef<any>();
    const { width, height } = useContainerDimensions(componentRef);

    useEffect(() => {

        console.log(`MediaRenderView::useEffect::refreshed...0...`);
    });


    return (<>

        {
            (fileInfo.file && componentRef) && <div ref={componentRef} className='media-render-view'>

            {
                /*
                <div> {(fileInfo.fileName)}, {width}, {height}</div>
                */
            }
                
                {(fileInfo.hasImage()) && (
                <>
                    <img draggable="true" onDragStart={(e) => e.preventDefault() } className='image-preview' src={URL.createObjectURL(fileInfo.file)} />
                    <div className="dimmed-text">{ fileInfo.fileName }</div>
                </>
                )}

                {(fileInfo.hasVideo()) && (
                    <>

                        <video draggable="true" onDragStart={(e) => e.preventDefault() } key={fileInfo.fileName} width="100%"  controls>
                            <source src={URL.createObjectURL(fileInfo.file)} type={fileInfo.fileType } />
                            Your browser does not support the video tag.
                        </video>
                        <div className="dimmed-text">{ fileInfo.fileName }</div>
                    </>

                    )
                }

                {(fileInfo.hasAudio()) && (
                    <>

                        { (width >= 400) &&
                            <>
                            <audio draggable="true" onDragStart={(e) => e.preventDefault() } key={fileInfo.fileName} src={URL.createObjectURL(fileInfo.file)} controls>
                                Your browser does not support the audio element.
                            </audio>
                            <div className="dimmed-text">{ fileInfo.fileName }</div>
                            </>
                        }
                        {
                        (width < 400) &&
                            <>
                              <video draggable="true" onDragStart={(e) => e.preventDefault() } width="100%" height="60px" controls>
                              <source src={URL.createObjectURL(fileInfo.file)} type={fileInfo.fileType } />
                              Your browser does not support the audio element.
                              </video>
                              <div className="dimmed-text">{ fileInfo.fileName }</div>
                            </>
                        }

                    </>

                )
                }
            </div>
        }

    </>);
}