import '../css/FlexCalendar.css';
import './css/CalendarExternal.css';
import '../../Sortable/useSortable.css';

import React, { createRef, RefObject, useContext, useLayoutEffect, useReducer } from 'react';
import { useState, useEffect, useRef } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from '../model/CalDateHelper';

import "/node_modules/font-awesome/css/font-awesome.min.css";
import { PopupModal } from './PopupModal';
import { FlexCalDBHelper } from '../db/FlexCalDBHelper';
import { CalDBContext } from '../../App';
import CalendarInputForm from './CalendarInputForm';
import { CalEventDataType, ICalEventData } from '../entity/ICalEventData';
import AutoHideWrapper from './AutoHideWrapper';
import CommonHelper from '../model/CommonHelper';
import { CalEventDataWrapper } from '../entity/CalEventData';
import { MediaRenderView } from '../view/MediaRenderView';
import { FileInfo } from '../entity/FileInfo';
import { useContainerDimensions } from '../hooks/useContainerDimensions';
import { getResponsiveContainerWidth, getResponsiveTextSize } from '../responsive/responsive';
import { useSortable } from '../../Sortable/useSortable';

export default function CalendarMonthContentCell({ id, date, children, handleContentChangeEvent }: {
    id: string;
    date: Date;
    children: any;
    handleContentChangeEvent: (date: Date)=>void;
}) {



    const flexCalDBHelper = useContext(CalDBContext);

    //
    const [eventDataList, setEventDataList] = useState<ICalEventData[]>([]);

    const [shouldShow, setShouldShow] = useState<boolean>(false);

    const [shouldEdit, setShouldEdit] = useState<boolean>(false);
    const [forceRefreshState, setForceRefreshState] = useState<number>(0);

    const componentRef = useRef<any>();
    const { width, height } = useContainerDimensions(componentRef);

    function forceRefresh() {
        //console.log(`CalendarMonthContentCell...forceRefresh...`);
        setForceRefreshState((new Date()).getTime());
    }

 

    
    //
    useEffect(() => {
        //console.log(`CalendarMonthContentCell...useEffect...${shouldShow}...${shouldEdit}...${forceRefreshState}...`);
        (async () => {
            await onRefresh();
        })();

    }, [shouldShow, shouldEdit, forceRefreshState]);


    //const textRef: RefObject<HTMLInputElement> = createRef();

    function onShowEdit(e: any) {

        e.preventDefault();
        e.stopPropagation();

        if (shouldEdit == true) {
            setShouldEdit(false);
        }
        else {
            setShouldEdit(true);
        }
    }

    function onShowPopup(e: any) {
        e.preventDefault();
        e.stopPropagation();

        setShouldShow(true);
    }
    function onHidePopup() {
        setShouldShow(false);
    }
    function saveEventData(eventData: ICalEventData) {
 
        eventData && flexCalDBHelper?.saveEventData(eventData);
        onHidePopup();

        //console.log(`onRefresh::AFTER deleteEventData...20...`);

        forceRefresh();

        handleContentChangeEvent(date);
    }


    function deleteEventData(e: any, eventData: ICalEventData) {

        e.preventDefault();
        e.stopPropagation();

        (async () => {

            await eventData && flexCalDBHelper?.deleteEventData(eventData);

            //console.log(`onRefresh::AFTER deleteEventData...10...`);

            await onRefresh();

            //console.log(`onRefresh::AFTER deleteEventData...20...`);

            forceRefresh();

            handleContentChangeEvent(date);
            
        })();



    }

    async function onRefresh() {
  
        const res = await flexCalDBHelper?.getTextEventsByDate(date)

        //console.log(`onRefresh::getTextEventsByDate::res==${JSON.stringify(res)}`);

        setEventDataList(res);
    }

    /*
    function handleDrag(ev: React.DragEvent<HTMLDivElement>): void {
  
        const id = (ev.target as HTMLDivElement).id;
        ev.dataTransfer.setData("text/plain", id);
        console.log(`handleDrag...id==${id}...`);
        console.log((ev.target as HTMLDivElement));
    }

    function allowDrop(ev: any) {
        ev.preventDefault();
    }

    function drop(ev: any) {
        ev.preventDefault();
        var data = ev.dataTransfer.getData("text");
        ev.target.appendChild(document.getElementById(data));
    }
    */

 
    return (

        componentRef && <div  id={ "MC_" + dateHelper.getKeyFromDate(date) }  key={date.toString()} ref={componentRef} className="calendar-month-content-cell"   /*onClick={ onClickArea }*/ >

            {
                shouldShow && <PopupModal
                    handleClosedEvent={onHidePopup} >

                    <CalendarInputForm date={date} saveEventData={(data: ICalEventData) => { saveEventData(data); }}></CalendarInputForm>

                </PopupModal>
            }

            <div className='horizontal-lhs-rhs-list  ' style={{ fontSize: getResponsiveTextSize(width) }} >

                {children}

                <div>
                    {
                        eventDataList && (eventDataList.length > 0) && (
                            <button className='btn-no-style' style={{ display: "inline-block", }} onClick={onShowEdit}> <i className={`fa fa-pencil   `}></i> </button>
                        )
                    }

                    &nbsp;&nbsp;

                    <button className='btn-no-style action-btn-text' style={{ display: "inline-block", }} onClick={onShowPopup}> <i className={`fa fa-plus  `}></i> </button>

                </div>


            </div>

            <br />


            {
                /*
                usednd-container
                */
            }
            <div className='  calendar-month-content-body'>
                {

                    eventDataList && eventDataList.map((eventData: ICalEventData) => {

                        const eventDataWrapper = new CalEventDataWrapper(eventData);
                        const fileInfo = eventDataWrapper.getFileInfo();

                        {
                            /*
                            draggable={true} className="usednd-draggable"
                            */
                        }
                        return (
                            <div id={eventData.eventId}  key={eventData.eventId} >

                                <div className="content-align-right">

                                    {
                                        shouldEdit && (
                                            <button onClick={(e: any) => { deleteEventData(e, eventData); }}>  <i className="fa  fa-minus"></i>   </button>
                                        )
                                    }

 
                                </div>

                             

                                <div>

                                    {(eventData.eventDataType == CalEventDataType.message && (eventData.eventText)) && (
                                            <div className='text-render-view' style={{ fontSize: getResponsiveTextSize(width) }}>
                                                <span>{ '*'.concat(eventData.eventText!) }</span> 
                                            </div>)}
                                    
                                    {(eventData.eventDataType == CalEventDataType.message && (eventData.eventText)) && (<div><br /></div>)}



                                    {
                                        fileInfo && <MediaRenderView fileInfo={fileInfo}></MediaRenderView>
                                    }



                                    <br />

                                </div>

                                <div style={{ height: '100%' }}></div>

                            </div>

                        );
                    })



                }

                <div className='calendar-cell-body-vertical-spacer'></div>

            </div>





        </div>
    );


}


