import '../css/FlexCalendar.css';
import React, { useContext, useRef } from 'react';
import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { dateHelper } from '../model/CalDateHelper';
import { stringSelector, curDateSelector, stateSelector, curMonthViewModelSelector, curWeekViewModelSelector } from '../redux/FlexCalSelectors';
import { actionGoNextMonth, actionGoNextWeek, actionGoPrevMonth, actionGoPrevWeek, actionSetCurrentDate } from '../redux/FlexCalActionCreators';
import FlexCalMonthViewModel from '../model/FlexCalMonthViewModel';
import { PrevNextView } from '../view/PrevNextView';
import { useContainerDimensions } from '../hooks/useContainerDimensions';
import { getResponsiveTextSize, getResponsiveTitleSize } from '../responsive/responsive';
import { FlexCalWeekHeader } from './FlexCalWeekHeader';
import { FlexCalWeekDayView } from './FlexCalWeekDayView';
import { useWindowScrollPositions } from '../hooks/useWindowScrollPositions';
import { FlexCalWeekControl } from './FlexCalWeekControl';
import FlexCalDayViewModel from '../model/FlexCalDayViewModel';

export default function FlexCalWeekViewContainer(props: any) {

  const componentRef = useRef<any>();
  const { width, height } = useContainerDimensions(componentRef);

  const { scrollX, scrollY } = useWindowScrollPositions();
  const [lastNarrowScrollY, setLastNarrowScrollY] = useState(0);

  useEffect(() => {

    console.log(`useEffect==width=${width}...scrollY=${scrollY}...lastNarrowScrollY=${lastNarrowScrollY}`);

    if (width < 880 && lastNarrowScrollY > 0) {


      /*
              window.scrollTo({
                top: lastNarrowScrollY,
                behavior: 'smooth'
              });
      */

      window.scrollTo({
        top: lastNarrowScrollY,
        behavior: 'smooth'
      });



    }
    else {
      if (width < 880) {
        setLastNarrowScrollY(scrollY);
      }
    }



  }, [width]);

  //
  const dispatch = useDispatch();
  const strings = useSelector(stringSelector);
  const curDate = useSelector(curDateSelector);
  const weekState = useSelector(curWeekViewModelSelector);
  const weekLabels = [
    strings.sun,
    strings.mon,
    strings.tue,
    strings.wed,
    strings.thu,
    strings.fri,
    strings.sat,
  ];
  const monthLabels = [
    strings.jan,
    strings.feb,
    strings.mar,
    strings.apr,
    strings.may,
    strings.jun,
    strings.jul,
    strings.aug,
    strings.sep,
    strings.oct,
    strings.nov,
    strings.dec,
  ];



  function doPrev() {
    console.log('do prev...');
    dispatch(actionGoPrevWeek());

  }
  function doNext() {
    console.log('do next...');
    dispatch(actionGoNextWeek());

  }

  function handleWeekItemClickEvent(dayVM: FlexCalDayViewModel) {
    console.log(`handleWeekItemClickEvent...${dayVM.date}`);
  }

  const onChildClick = (e: any, date: Date) => {
    console.log(`FlexCalApp::getDayContentCells::onClick==... ...`);
   
    e.preventDefault();
    e.stopPropagation();

    dispatch(actionSetCurrentDate(date));
  }


  return (

    <div ref={componentRef} className="FlexCalWeekContainer">

      <div className="FlexCalWeekHeader  sticky-to-top-under ">


        <PrevNextView doPrev={() => { doPrev(); }} doNext={() => { doNext(); }}>

          <div>

            <div style={{ fontSize: getResponsiveTitleSize(width) }} className="center">
              {monthLabels[curDate.getMonth()]}
              &nbsp;
              {curDate.getFullYear()}
            </div>

          <div className='FlexCalHideWhenBigControl'>
            {
             ( <FlexCalWeekControl handleWeekItemClickEvent={ handleWeekItemClickEvent }></FlexCalWeekControl>) 
            }
          </div>
             
             
          </div>
 
        </PrevNextView>



      </div>

      <br />

   
       

      <div className="FlexCalWeekBody">


        {
          weekState && weekState.map((week, index) => {

            return (<div key={index}
              className= {`${dateHelper.isSameDate(week.date, curDate) ? "selected-cell" : ""} `}
              onClick={ (e: any)=> onChildClick(e, week.date)}
              >



              {
                props.children && props.children.filter((comp: any) => {
                  const comp_date = new Date(comp.props.date);


                  return (comp_date.getFullYear() === week.date.getFullYear()
                    && (comp_date.getMonth() === week.date.getMonth())
                    && (comp_date.getDate() === week.date.getDate())
                  );

                })
                  .map((comp: any ) => {

                     
                    return (<FlexCalWeekDayView id={'D' + week.date.getDate()}  key={'D_' + index + '_' + week.date.getDate()} weekLabel={weekLabels[index]} calDayViewModel={week}>
                      {comp} 
                    </FlexCalWeekDayView>);
 
                  })
              }



            </div>)
          })

        }
      </div>


    </div>
  );
}


