import { uniqueId } from 'underscore';
import { CalEventDataType, ICalEventData } from '../entity/ICalEventData';
import CommonHelper from '../model/CommonHelper';
import FlexCalDB from './FlexCalDB';
import { dateHelper } from '../model/CalDateHelper';
import { CalYearDayHasData  } from '../entity/ICalYearDayVM';
import { IFlexCalDBChangeListener } from './IFlexCalDBChangeListener';

 
 
export class FlexCalDBHelper {

    db: FlexCalDB;

    dbChangeListeners: IFlexCalDBChangeListener[] = [];

    registerDbChangeEvent(listener: IFlexCalDBChangeListener) {
        console.log(`FlexCalDBHelper::registerDbChangeEvent...0...${listener}...`);
        this.dbChangeListeners.push(listener);
    }

    unregisterDbChangeEvent(listener: IFlexCalDBChangeListener) {
        console.log(`FlexCalDBHelper::unregisterDbChangeEvent...0...${listener}...`);
        this.dbChangeListeners = this.dbChangeListeners.filter(l=>l !== listener);
    }

    notifyDbChangeEvent(date: Date) {
        this.dbChangeListeners.forEach(l=>{
            console.log(`FlexCalDBHelper::notifyDbChangeEvent...0...${l}...`);
            l.onDbContentChangedEventOccurred(date);
        });
    }

    constructor() {

        console.log(`FlexCalDBHelper::constructor...0...`);

        this.db = new FlexCalDB();
    }

    async getTextEventsByDate(date: Date) {

        //console.log(`DBHelper::getTextEventsByDate...0...${date}...`);
        //return await this.db.transaction('r', this.db.textEvents, async () => {

            // .filter((c)=>(c.eventDate! == date))
         return (await this.db.textEvents.toArray()).map((c)=>{

            
                //console.log('DBHelper::getTextEventsByDate...10...textEvents::', c.eventId,   (new Date(c.eventDate)).getTime(), new Date(c.eventDate), " VS ", new Date(c.eventDate!), (new Date(date)).getTime(), ( (new Date(c.eventDate!)).getTime() == (new Date(date)).getTime() )  ,c.eventText);

                return c;

            })
            .filter((c)=>{

                const c_date = new Date(c.eventDate!);
                return (c_date.getFullYear()==date.getFullYear()) 
                        && (c_date.getMonth()==date.getMonth()) 
                        && (c_date.getDate()==date.getDate());
                
            })
            .sort((c1, c2)=>{ 

                const c1_date = new Date(c1.updateDate).getTime();
                const c2_date = new Date(c2.updateDate).getTime();

                if (c1_date < c2_date ) {
                    return -1;
                } else if (c2_date < c1_date ) {
                    return 1;
                } else {
                    return 0;
                }

            })
            ;

           

        //});
    }

     
    async getTextEventsByYear(date: Date) {
       
        const yyyy = date.getFullYear();

        console.log(`getTextEventsByYear...0...${yyyy}...`);
 
        let existsMap: CalYearDayHasData = {};
        

          (await this.db.textEvents.toArray())
            .filter((c)=>{
                console.log(`getTextEventsByYear...10...${c.eventDate.getFullYear()} VS ${yyyy}...${( c.eventDate.getFullYear() == yyyy)}...`);
                return ( c.eventDate.getFullYear() == yyyy);
            })
          .forEach((c)=>{
                const key = dateHelper.getKeyFromDate(c.eventDate);

                console.log(`getTextEventsByYear...20...key==${key}...`);

                existsMap[key] = true;
                console.log(`getTextEventsByYear...30...key==${key}...${existsMap[key]}`);

            });

            console.log(`getTextEventsByYear...40...existsMap==${ existsMap.size }...`);

            return existsMap;
    }



    async getTextEventsByYearMonth(date: Date) {
       
        const yyyy = date.getFullYear();
        const mm = date.getMonth();

        console.log(`getTextEventsByYear...0...${yyyy}...`);
 
        let existsMap: CalYearDayHasData = {};

          (await this.db.textEvents.toArray())
            .filter((c)=>{
                console.log(`getTextEventsByYear...10...${c.eventDate.getFullYear()} VS ${yyyy}...${( c.eventDate.getFullYear() == yyyy)}...`);
                return ( c.eventDate.getFullYear() == yyyy) && ( c.eventDate.getMonth() == mm);
            })
          .forEach((c)=>{
                const key = dateHelper.getKeyFromDate(c.eventDate);

                console.log(`getTextEventsByYear...20...key==${key}...`);

                existsMap[key] = true;
                console.log(`getTextEventsByYear...30...key==${key}...${existsMap[key]}`);
                 
            });

            console.log(`getTextEventsByYear...40...existsMap==${ existsMap.size }...`);

            
            return existsMap;
    }


    async getTextEventsByDateRange(min_date: Date, max_date: Date) {
  
        const adjusted_min_date = new Date(min_date);
        const adjusted_max_date = new Date(max_date);
        adjusted_min_date.setHours(0);
        adjusted_min_date.setMinutes(0);
        adjusted_min_date.setDate(0);
        adjusted_max_date.setHours(23);
        adjusted_max_date.setMinutes(59);
        adjusted_max_date.setDate(59);

        console.log(`getTextEventsByDateRange...0...min_date=${min_date}...max_date=${max_date}...`);
 
        let existsMap: CalYearDayHasData = {};

          (await this.db.textEvents.toArray())
            .filter((c)=>{
                console.log(`getTextEventsByDateRange...10...`);
                return ( c.eventDate >= min_date) && ( c.eventDate <= max_date);
            })
          .forEach((c)=>{
                const key = dateHelper.getKeyFromDate(c.eventDate);

                console.log(`getTextEventsByDateRange...20...key==${key}...`);

                existsMap[key] = true;
                console.log(`getTextEventsByDateRange...30...key==${key}...${existsMap[key]}`);
                 
            });

            console.log(`getTextEventsByDateRange...40...existsMap==${ existsMap.size }...`);

            
            return existsMap;
    }




    async getTextEventsByMonth(date: Date) {

        //console.log(`DBHelper::getTextEventsByMonth...0...`);
       
        const yyyy = date.getFullYear();
        const mm = date.getMonth();

         return (await this.db.textEvents.toArray()).map((c)=>{

            
                //console.log('DBHelper::getTextEventsByDate...10...textEvents::', c.eventId, (new Date(c.eventDate)).getTime(), " VS ", (new Date(date)).getTime(), ( (new Date(c.eventDate!)).getTime() == (new Date(date)).getTime() )  ,c.eventText);

                return c;

            })
            .filter((c)=>{
                const c_date = new Date(c.eventDate!);
                const c_yyyy = c_date.getFullYear();
                const c_mm = c_date.getMonth();

                return ( c_yyyy == yyyy) && (c_mm == mm);
            })
            .sort((c1, c2)=>{ 

                const c1_date = new Date(c1.updateDate).getTime();
                const c2_date = new Date(c2.updateDate).getTime();

                if (c1_date < c2_date ) {
                    return -1;
                } else if (c2_date < c1_date ) {
                    return 1;
                } else {
                    return 0;
                }

            });
    }


    //eventId, eventDate, eventText
    async saveEventData(eventData: ICalEventData) {

        await this.db.transaction('rw', this.db.textEvents, async () => {
               
            // Transaction block

            this.db.textEvents.add(eventData);
             
            await (await this.db.textEvents.filter((c)=>(c.eventId!.length >= 0)).toArray()).forEach((c)=>{

                //console.log('textEvents::', c.eventId, c.eventDate, c.eventText);
            })

            this.notifyDbChangeEvent(eventData.eventDate);

          });
    }

    async updateEventData(eventData: ICalEventData) {

        await this.db.transaction('rw', this.db.textEvents, async () => {

            console.log(`updateEventData...10...${eventData.id}...${eventData.eventText}`);

            eventData.id && this.db.textEvents.put(eventData, eventData.id);
             
            await (await this.db.textEvents.filter((c)=>(c.eventId!.length >= 0)).toArray()).forEach((c)=>{

            });

            this.notifyDbChangeEvent(eventData.eventDate);
        });
    }

    async deleteEventData(eventData: ICalEventData) {

        await this.db.transaction('rw', this.db.textEvents, async () => {

            console.log(`deleteEventData...10...${eventData.id}...${eventData.eventText}`);

            eventData.id && this.db.textEvents.delete(eventData.id);
             
            await (await this.db.textEvents.filter((c)=>(c.eventId!.length >= 0)).toArray()).forEach((c)=>{

            });

            this.notifyDbChangeEvent(eventData.eventDate);
        });
    }
}


export const flexCalHelper = new FlexCalDBHelper();