import '../css/FlexCalendar.css';

export function PrevNextView({children, doPrev, doNext}:{
    children: any;
    doPrev: ()=>void;
    doNext: ()=>void;
}) {

    function doPrevClicked(e: any) {
        e.preventDefault();
        e.stopPropagation();
        doPrev();
    }

    function doNextClicked(e: any) {
        e.preventDefault();
        e.stopPropagation();
        doNext();
    }

    return (<div className="PrevNextView">
        <button className='btn-no-style' onClick={ doPrevClicked }><i className="fa fa-chevron-left "></i></button>   
        &nbsp;&nbsp;
        <span> { children } </span>
        &nbsp;&nbsp;
        <button className='btn-no-style' onClick={ doNextClicked }><i className="fa fa-chevron-right "></i></button>
    </div>);

}

