 
import CommonHelper from "./CommonHelper";

const DEFAULT_DATE = new Date();

export default class FlexCalDayViewModel {

    date: Date = DEFAULT_DATE;
    public isCurrentMonth: boolean = false;


    setDate(_date: Date, calMonthDate: Date) {
        const curDate = new Date();
        this.date = _date;
        this.isCurrentMonth = (this.date.getFullYear() == calMonthDate.getFullYear())
            && (this.date.getMonth() == calMonthDate.getMonth());
    } 
 
    get yearLabel() {
        return this.date.getFullYear();
    }

    get monthLabel() {
        return 1 + this.date.getMonth();
    }

    get dayLabel() {
        return this.date.getDate();
    }

    get keyLabel() {
        return `${this.yearLabel}_${this.monthLabel}_${this.dayLabel}${CommonHelper.generateUUIDKey()}`;
    }

}